export class Test {
  url: string;
  sessions: number;
  time: number;
  type: string;

  constructor(obj: Test = {} as Test) {
    const {
      url = '',
      sessions = 0,
      time = 0,
      type = ''
    } = obj;

    this.url = url;
    this.sessions = sessions;
    this.time = time;
    this.type = type;
  }
}



