import { Component, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { WeatherService } from '../../weather.service';
import { Chart } from 'chart.js';

@Component({
  selector: 'app-teststats',
  templateUrl: './teststats.component.html',
  styleUrls: ['./teststats.component.css']
})
export class TeststatsComponent implements OnInit {
  constructor(private _weather: WeatherService) { }
  // Chart JS
  chart = []; // This will hold our chart info
  ngOnInit() {
    this._weather.dailyForecast()
      .subscribe(res => {

        let temp_max = res['list'].map(res => res.main.temp_max);
        let temp_min = res['list'].map(res => res.main.temp_min);
        let alldates = res['list'].map(res => res.dt);

        let weatherDates = [];
        alldates.forEach((res) => {
          let jsdate = new Date(res * 1000);
          weatherDates.push(jsdate.toLocaleTimeString('en', { year: 'numeric', month: 'short', day: 'numeric' }))
        });

        // this.chart = new Chart('canvas', {
        //   type: 'line',
        //   data: {
        //     labels: weatherDates,
        //     datasets: [
        //       {
        //         data: temp_max,
        //         borderColor: "#3cba9f",
        //         fill: false
        //       },
        //       {
        //         data: temp_min,
        //         borderColor: "#ffcc00",
        //         fill: false
        //       },
        //     ]
        //   },
        //   options: {
        //     legend: {
        //       display: false
        //     },
        //     scales: {
        //       xAxes: [{
        //         display: true
        //       }],
        //       yAxes: [{
        //         display: true
        //       }],
        //     }
        //   }
        // });
        this.chart = new Chart('canvas', {
          type: 'line',
          data: {
            labels: ['02:17:12', '02:17:42', '02:18:12', '02:18:42', '02:19:12', '02:19:42', '02:20:12', '02:20:42', '02:21:12', '02:21:42', '02:22:12', '02:22:42'],
            datasets: [{
              yAxisID: 'S',
              label: 'Sessions',
              data: [50, 50, 50, 50, 50, 50, 50, 50, 50, 50, 50, 0],
              backgroundColor: 'transparent',
              borderColor: '#07f',
              borderWidth: 3,
              pointBackgroundColor: '#fff',
              pointBorderColor: '#06e',
              pointBorderWidth: 4
            }, {
              yAxisID: 'A',
              label: 'Answer time (s)',
              data: [1.4, 1.2, 1.6, 1.7, 1.8, 2.8, 4.2, 3.5, 3.4, 3.2, 3.1],
              backgroundColor: 'transparent',
              lineTension: 0.35,
              borderColor: '#2d3',
              borderWidth: 3,
              pointBackgroundColor: '#fff',
              pointBorderColor: '#1c2',
              pointBorderWidth: 4
            }]
          },
          options: {
            scales: {
              yAxes: [{
                id: 'S',
                type: 'linear',
                position: 'left',
                ticks: {
                  min: 0,
                  max: 50
                }
              }, {
                id: 'A',
                type: 'linear',
                position: 'right',
                ticks: {
                  beginAtZero: true
                }
              }]
            },
            legend: {
              display: true
            }
          }
        });

      });
  }

  // Mat-Table
  displayedColumns = ['url', 'created', 'sessions', 'time', 'type'];
  dataSource = new MatTableDataSource(ELEMENT_DATA);

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  //

}
export interface Element {
  url: string;
  created: string;
  sessions: number;
  time: string;
  type: string;
}

const ELEMENT_DATA: Element[] = [
  {url: 'hereisverylongdomainname.in.space', created: '2018/05/19 03:02:55', sessions: 	10, time: '5 minutes', type: 'Gradual Test'},
  {url: 'de.ifmo.ru', created: '2017/04/13 02:02:55', sessions: 30, time: '15 minutes', type: 'Gradual Test'},
];



