import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import {ToastrService} from 'ngx-toastr';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  constructor(private toastr: ToastrService) { }

    // this.userService.getUserClaims().subscribe((data: any) => {
    //   this.userClaims = data;
    //
    // });
  email2;
  isError = false;
  profile;
  getProfile() {
    this.profile = {email: 'whoisthisniggathefuckaboi@yandex.ru', pass: 'heyGuysEverythingIsNice?'};
  }
  onSave(curP: string, newP: string, repP: string) {
    if (curP !== this.profile.pass || newP !== repP)
    {
      this.isError = true;
    }
    else
    {
      this.profile.pass=newP;
      this.isError= false;
    }

    if (!this.isError)
      this.toastr.success('Profile has been successfully updated');
    else
      this.toastr.error('Error: current password or new passwords do not match');

  }
  ngOnInit() {
    this.getProfile();
  }

}



