import { Component, OnInit } from '@angular/core';
import { Test } from '../../test';
import {objectify} from 'tslint/lib/utils';
import { HardtestService } from '../../shared/hardtest.service';
import {FormControl, FormGroupDirective, NgForm, Validators} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';

/** Error when invalid control is dirty, touched, or submitted. */
export class MyErrorStateMatcher implements ErrorStateMatcher {
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}
@Component({
  selector: 'app-testform',
  templateUrl: './testform.component.html',
  styleUrls: ['./testform.component.css']
})
export class TestformComponent implements OnInit {
  urlPattern = "^http(s?):\\/\\/(.+)$";

  urlControl = new FormControl('', [
    Validators.required,
    Validators.pattern(this.urlPattern),
  ]);

  matcher = new MyErrorStateMatcher();

  test = new Test({url: '', sessions: 0, time: 0, type: '0'});
  stopRespond: string;
  startRespond: any;
  types = [
    'Gradual Test',
    'Stress Test'
  ]

  constructor(private hardtestService: HardtestService) { }

  ngOnInit() {
  }

  stopHardtest(): void {
    this.hardtestService.stopHardtest()
      .subscribe((respond) => {
        this.stopRespond = respond;
      });
  }

  startHardtest(test: Test): void {
    this.hardtestService.startHardtest(test)
      .subscribe((respond) => {
        this.startRespond = respond;
      });
  }


}



